* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  background: #122f41;
}

body.locked {
  overflow: hidden;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-right: 15px;
  padding-left: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::before,
::after {
  font-family: "fontawesome";
}

.burger::before {
  content: "\f805";
  display: inline-block;
  font-size: 32px;
  position: fixed;
  top: 30px;
  right: 30px;
  z-index: 10;
  color: #fff;
  display: none;
}

@media (max-width: 992px) {
  .burger::before {
    display: inline-block;
  }
}

.cancel::before {
  content: '\f410';
}

.wrapper::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.9;
  z-index: 998;
  display: none;
}

.wrapper.active::before {
  display: block;
}

.slider {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: 1s;
}

.slider.active {
  opacity: 1;
  visibility: visible;
}

.slider-slide {
  width: 100%;
  height: 100%;
}

