    align-items: start;
  }
  .positive__adv .positive__img, .positive__dis .positive__img {
    position: absolute;
    top: 30%;
    right: 30%;
  }
}

@media (max-width: 768px) and (max-width: 400px) {
  .positive__adv .positive__img, .positive__dis .positive__img {
    top: 10%;
    right: 0;
  }
}

@media screen and (max-width: 768px) and (orientation: landscape) {
  .positive__adv .positive__img, .positive__dis .positive__img {
    position: relative;
    right: 0;
