@import "./common/root" , 
        "./layout/grid" , 
        "./common/common";

.wrapper {
    // position: fixed;
    // top: 0;
    // left: 0;
    // width: 100%;
    // height: 100%;
    // overflow: hidden;

}

.wrapper::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(0,0,0);
    opacity: 0.9;
    z-index: 998;
    display: none;
}

.wrapper.active::before {
    display: block;
}

.slider {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transition: 1s;
    &.active {
        opacity: 1;
        visibility: visible;
    }

    &-slide {
        width: 100%;
        height: 100%;
    }
}

