.intro {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: 1s;
    padding: 0 10px;

    &.slide {
        opacity: 0;
        visibility: hidden;
        top: -100%;
    }

    
    &__uptitle {
        font-family: "Maximian";
        text-transform: uppercase;
        font-size: clamp(42px,calc(71vw/14.4),80px);
        line-height: clamp(62px,calc(100vw/14.4),110px);
        background: linear-gradient(180deg, rgba(12, 11, 11, 0.2) 0%, rgba(12, 11, 11, 0) 100%),linear-gradient(180deg, #00FDEE 0%, rgba(0, 253, 238, 0) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

    }

    &__img {
        width: 100%;
        height: clamp(100px,calc(200vw/14.4),250px);
        max-height: 100%;
        object-fit: cover;
    }
    &__button {
        text-transform: uppercase;
        font-family: "Rapidtech";
        font-size: clamp(42px,calc(52vw/14.4),60px);
        line-height: clamp(42px,calc(52vw/14.4),60px);
        text-align: center;
        color: #fff;
        background: linear-gradient(180deg, #13124A -31.23%, #1FF7F7 50.68%, #1B1A58 122.85%);
        border-radius: 25px;
        border: none;
        padding: 15px 20px;
        cursor: pointer;

    }
}

.pro {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    &__content {
        width: 50%;
        height: 100%;
        background: rgb(47,137,227);
        background: linear-gradient(100deg, rgba(47,137,227,1) 0%,rgba(47,137,227,0.3) 70%,rgba(18,47,65,0.7) 90% ,rgba(18,47,65,1) 100%);
        -webkit-box-shadow: -29px -17px 51px 0px rgba(18,47,65,1) inset;
        -moz-box-shadow: -29px -17px 51px 0px rgba(18,47,65,1) inset;
        box-shadow: -29px -17px 51px 0px rgba(18,47,65,1) inset;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        @include media(1200px){
            width: 35%;
        }
        @include media($md) {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 20%;
        }

        // overflow: hidden;

        &-border {
            width: clamp(30px,calc(50vw/14.4),60px);
            height: 150%;
            background: #fff;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            position: absolute;
            top: -10%;
            right: 0;
            transform: rotate(15deg);

            @include media(1200px){
                transform: rotate(6deg);
            }
            @include media($md){
                display: none;
            }

            &-part {
                flex: auto;
                border-top: 1px solid #000;
                border-bottom: 1px solid #000;
                border-left: 5px solid #000;
                border-right: 5px solid #000;
            }
        }
    }



    &__desc {
        width: 40%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        // justify-content: center;

        @include media(1200px){
            width: 55%;
        }
        @include media($md){
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 80%;
            text-align: center;
            padding-top: 1%;
        }

        &-title {
            font-family: "RedRose";
            font-weight: 700;
            font-size: clamp(32px,calc(72vw/14.4),80px);
            line-height: clamp(40px,calc(82vw/14.4),90px);
            border-bottom: 2px solid #fff;
            color: #fff;
        }

        &-list {
            display: flex;
            flex-direction: column;
            width: 100%;
            height: 100%;
            padding: 25px 0;

            & li {
                display: flex;
                align-items: center;
                gap: 20px;

                & span {
                    font-family: "serif";
                    font-size: clamp(14px,calc(40vw/14.4),45px);
                    line-height: clamp(20px,calc(44vw/14.4),55px);
                    color: #fff;
                }
            }

            @include media($md){
                padding: 0 20px;
                gap: 20px;
                justify-content: space-evenly;
            }
            @media only screen and (orientation:landscape) {
                flex-direction: row;
                flex-wrap: wrap;
                gap: 20px;
            }
        }

        &-img {
            width: clamp(30px,calc(50vw/14.4),60px);
            height: clamp(30px,calc(50vw/14.4),60px);
            object-fit: contain;
        }

        &-subtitle {
            font-family: "Kurale";
            font-size: clamp(18px,calc(40vw/14.4),50px);
            line-height: clamp(24px,calc(60vw/14.4),70px);
            color: #fff;
        }
    }
    
    &__img {
        // position: absolute;
        // top: 3%;
        // left: 3%;
        // max-width: clamp(200px,calc(900vw/14.4),1200px);
        // width: 100%;
        // height: clamp(300px,calc(700vw/14.4),800px);

        width: 400px;
        height: 500px;
        position: absolute;
        top: -15%;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;

        & img {
            // width: 100%;
            // height: 100%;
            // object-fit: contain;

            padding-top: 10%;
            width: 100%;
            height: 100%;
            display: block;
            margin: 0 auto;
            border: none;
            object-fit: cover;
        }

        @include media($md) {
            height: 110%;
            width: clamp(100px,calc(150vw/14.4),170px);
            inset: 0;
        }
    }
}

.nav {

    &__list {
        position: fixed;
        z-index: 999;
        left: 0;
        height: clamp(30px,calc(60vw/14.4),100px);
        width: 100%;
        top: -100%;
        transition: 0.5s;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        @include media($xs) {
            width: 100%;
            overflow: scroll;
        }

        @include media($sm) {
            flex-direction: column;
            justify-content: space-between;
            width: 50%;
            height: 100%;
            background: #fff;
            align-items: start;
            padding-left: 10%;
            top: 0;
            left: -100%;

            &.active {
                left: 0;
            }
        }
        
        &.active {
            top: 0;
        }
    }

    &__item {
        flex: auto;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #000;
        background: #fff;

        @include media($sm) {
            border: none;
            transform: translateX(-60px);
            opacity: 0;
            transition: 1s;

            &.active {
                transform: translateX(0);
                opacity: 1;
            }
        }



    }

    &__link {
        font-family: "Gochi";
        font-size: clamp(24px,calc(40vw/14.4),50px);
        line-height: clamp(30px,calc(46px,56px));
        color: #000;
    }

    &__logo {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }

    &__lang {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    

    &__title {
        position: fixed;
        top: 0;
        left: 5%;
        padding: 5px clamp(5px,calc(10vw/14.4),15px);
        font-size: clamp(18px,calc(24vw/14.4),32px);
        line-height: clamp(20px,calc(36vw/14.4),40px);
        background: #fff;
        font-family: "Gochi";
        border-bottom-right-radius: clamp(10px,calc(20vw/14.4),30px);
        border-bottom-left-radius: clamp(10px,calc(20vw/14.4),30px);
        cursor: pointer;
        opacity: 0;
        visibility: hidden;
        transition: 0.6s;
        &.active {
            opacity: 1;
            visibility: visible;
        }
    }

    &__cancel {
        font-size: 34px;
        position: absolute;
        top: 10px;
        right: 10px;
        display: none;
    
        @include media(500px) {
            display: inline-block;
        }
    }
}


.social {
    display: flex;
    padding: clamp(30px,calc(50vw/14.4),55px) clamp(20px,calc(50vw/14.4),60px) clamp(20px,calc(50vw/14.4),60px) clamp(20px,calc(50vw/14.4),60px);
    background: rgb(146,65,192);

    @include media(500px) {
        flex-direction: column;
    }

    &__names {
        height: 100%;
        width: 60%;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        border-right: 5px solid #fff;
        border-radius: 5px;

        @include media(500px) {
            border-right: none;
            width: 100%;
            border-bottom: 5px solid #fff;
        }
    }

    &__desc {
        height: 100%;
        width: 40%;

        @include media(500px) {
            width: 100%;
        }
    }


    &__img {
        width: clamp(40px,calc(150vw/14.4),250px);
        height: clamp(40px,calc(150vw/14.4),250px);
        position: relative;
        
        & img {
            width: 100%;
            height: 100%;
            inset: 0;
            object-fit: contain;
        }
    }

    &__card {
        display: flex;
        align-items: center;
    }

    &__name {
        font-family: "Righteous";
        font-size: clamp(24px,calc(80vw/14.4),120px);
        line-height: clamp(28px,calc(90vw/14.4),130px);
    }

    &__desc {
        font-family: "Righteous";
        font-size: clamp(18px,calc(38vw/14.4),54px);
        line-height: clamp(24px,calc(54vw/14.4),60px);
        padding: clamp(10px,calc(20vw/14.4),25px);
        color: #fff;
    }
}


.design {
    display: flex;
    background: rgba(255, 153, 0, 1);

    @include media(450px) {
        flex-direction: column;
    }

    &__web ,
    &__vector {
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: start;
        align-items: center;
        padding: clamp(40px,calc(50vw/14.4),60px) clamp(10px,calc(25vw/14.4),30px) clamp(20px,calc(50vw/14.4),60px) clamp(10px,calc(25vw/14.4),30px);
        gap: 10px;

        @include media(450px) {
            width: 100%;
            height: 50%;
            gap: 0;
        }
    }
    &__vector {
        background: #fff;
    }

    &__wave {
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        transform: rotate(90deg);
    }


    &__web-title,
    &__vector-title {
        font-family: "Gochi";
        font-weight: 700;
        font-size: clamp(16px,calc(50vw/14.4),54px);
        line-height: clamp(18px,calc(55vw/14.4),60px);
    }
    &__web-title {
        color: #fff;
    }
    &__vector-title {
        color: rgba(255, 153, 0, 1);
    }

    &__web-text,
    &__vector-text {
        font-family: "Roboto";
        font-size: clamp(12px,calc(32vw/14.4),36px);
        line-height: clamp(14px,calc(36vw/14.4),42px);
        color: #000;
    }

    & img {
        width: 100%;
        height: 50%;
        object-fit: contain;
        margin-bottom: 15px;
    }
}

.video {
    padding: clamp(30px,calc(50vw/14.4),55px) clamp(20px,calc(50vw/14.4),60px) clamp(20px,calc(50vw/14.4),60px) clamp(20px,calc(50vw/14.4),60px);
    background: #fff;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    &__title {
        font-family: "Bruno";
        font-size: clamp(24px,calc(50vw/14.4),54px);
        line-height: clamp(30px,calc(54vw/14.4),60px);
        color: #000;
        margin-bottom: clamp(10px,calc(25vw/14.4),35px);
        text-align: center;
    }

    &__video {
        width: 60%;
        height: clamp(150px,calc(350vw/14.4),450px);
        margin-bottom: clamp(10px,calc(25vw/14.4),35px);
        position: relative;
        

        & div {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border-radius: 25px;
            background: red;
        }

        @media screen and (orientation:landscape) {
            height: clamp(50px,calc(200vw/14.4),200px);
        }
    }

    &__cards {
        width: 100%;
        display: flex;
        justify-content: space-between;

        @include media($sm) {
            flex-direction: column;
            align-items: flex-start;
            gap: clamp(10px,calc(20vw/14.4),25px);
        }

        @media screen and (orientation:landscape) {
            flex-direction: row;
            justify-content: space-around;
            gap: 5px;
        }
    }

    &__card {
        display: flex;
        flex-direction: column;
        gap: clamp(5px,calc(15vw/14.4),25px);
        align-items: center;
        text-align: center;

        @include media($sm) {
            flex-direction: row;
        }
        @media screen and (orientation:landscape) {
            flex-direction: column;
            // justify-content: space-around;
        }
    }

    &__img {
        width: clamp(35px,calc(80vw/14.4),100px);
        height: clamp(35px,calc(80vw/14.4),100px);
        position: relative;

        @include media($sm ){
            width: clamp(45px,calc(100vw/14.4),120px);
            height: clamp(45px,calc(100vw/14.4),120px);
        }

        & img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__text {
        font-family: "Cabin";
        font-size: clamp(12px,calc(27vw/14.4),34px);
        line-height: clamp(16px,calc(30vw/14.4),34px);
        padding: 0 clamp(10px,calc(20vw/14.4),25px);

        @include media($sm) {
            font-size: clamp(18px,calc(34vw/14.4),50px);
            line-height: clamp(20px,calc(34vw/14.4),50px);
        }

        @media screen and (orientation:landscape) {
            font-size: clamp(12px,calc(34vw/14.4),50px);
            line-height: clamp(14px,calc(34vw/14.4),50px);
        }
        
    }
}

.positive {
    padding: clamp(40px,calc(50vw/14.4),55px) clamp(20px,calc(50vw/14.4),60px) clamp(20px,calc(50vw/14.4),60px) clamp(20px,calc(50vw/14.4),60px);
    display: grid;
    grid-template: 1fr / 1fr clamp(100px,calc(200vw/14.4),250px) 1fr;
    background: rgb(225,167,46);
    justify-content: space-around;
    align-items: center;
    position: relative;

    @include media($sm) {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }

    @media screen and (orientation: landscape) {
        display: flex;
        flex-direction: row;
    }

    &__adv,
    &__dis {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        @include media($sm) {
            width: 100%;
            position: relative;
            align-items: start;

            & .positive__img {
                position: absolute;
                top: 30%;
                right: 30%;

                @include media(400px) {
                    top: 10%;
                    right: 0;
                }

                @media screen and (orientation: landscape) {
                    position: relative;
                    right: 0;
                }
            }
        }

        @media screen and (orientation: landscape) {
            align-items: center;
        }
        
    }


    &__img {
        width: clamp(40px,calc(120vw/14.4),150px);
        height: clamp(40px,calc(120vw/14.4),150px);
        position: relative;

        & img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

    }

    &__boxes {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

        @media only screen and (orientation: portrait) and (max-width: 668px) {
            width: 100%;
            flex-direction: row;
        }

        @media only screen and (orientation: landscape) and (max-width: 668px) and (max-height: 360px) {
            display: none;
        }

        @media only screen and (orientation: landscape) and (max-width: 668px) {
            height: auto;
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 5px;
            flex-direction: row;
        }
        
    }
    
    &__title {
        font-family: "Bubblegum";
        font-size: clamp(24px,calc(80vw/14.4),100px);
        line-height: clamp(30px,calc(96vw/14.4),106px);
    }

    &__list {
        list-style: initial;

        & li {
            font-family: "CantoraOne";
            font-size: clamp(18px,calc(45vw/14.4),55px);
            line-height: clamp(24px,calc(50vw/14.4),75px);
            @include media($sm) {
                margin-left: 25px;
            }
        }

    }
}

.detail {
    padding: 50px clamp(15px,calc(50vw/14.4),60px);
    // text-align: justify;
    text-align: center;
    // display: flex;
    // flex-direction: column;
    // justify-content: space-evenly;
    // align-items: center;


    &__text {
        font-family: "Roboto";
        font-size: clamp(18px,calc(40vw/14.4),45px);
        color: #fff;
        line-height: clamp(30px,calc(80vw/14.4),90px);
        vertical-align: middle;

        @media screen and (orientation: portrait) {
            font-size: clamp(24px,calc(50vw/14.4),60px);
        }

        @media screen and (orientation: landscape) {
            font-size: clamp(14px,calc(30vw/14.4),40px);
        }
    }


    &__box {
        width: clamp(50px,calc(350vw/14.4),400px);
        height: clamp(20px,calc(50vw/14.4),60px);
        display: inline-block;
        vertical-align: middle;
        border-radius: 15px;
        margin: 0 10px;
    }
}


.copy {
    padding: clamp(30px,calc(50vw/14.4),60px) clamp(15px,calc(50vw/14.4),60px);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: #fff;

    @include media(1064px) {
        justify-content: start;
    }


    &__title {
        margin: 0 auto;
        & span {
            font-size: clamp(40px,calc(80vw/14.4),90px);
            line-height: clamp(50px,90vw/14.4),100px;
            color: #000;
        }

        @include media(1064px) {
            margin-bottom: 10px;
        }
    }

    &__content {
        width: 100%;
        display: flex;

        @include media(1064px) {
            flex-direction: column;
        }
    }

    &__text {
        font-family: "Boho";
        font-size: clamp(50px,calc(70vw/14.4),80px);
        line-height: clamp(34px,calc(80vw/14.4),90px);
        text-align: justify;
        width: 60%;
        height: 100%;
        padding-right: 20px;
        border-right: 5px solid #000;

        @include media(1064px) {
            width: 100%;
            border-right: none;
            border-bottom: 5px solid #000;
            padding-right: 0;
            padding-bottom: 10px;
        }
    }

    &__note {
        height: 100%;
        width: 40%;
        padding-left: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        @include media(1064px) {
            display: none;
        }

        & div {
            width: 100%;
            height: 10px;
            background: #000;
            border-radius: 5px;
        }
    }
}



.footer {
    padding: clamp(30px,calc(50vw/14.4),60px) clamp(15px,calc(50vw/14.4),60px);
    background: rgb(52,143,211);
    display: flex;
    
    @include media(1280px) {
        flex-direction: column;
    }
    
    &__portfolio {
        width: 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 50px;

        @include media(1280px) {
            width: 100%;
            text-align: justify;
        }

        &-text {
            font-size: clamp(12px,calc(18vw/14.4),22px);
            line-height: clamp(16px,calc(22vw/14.4),26px);
            font-family: "Roboto";
            text-align: justify;
            color: #fff;
            margin-top: 15px;
            
            & span {
                font-weight: 700;
            }
        }
    }

    &__title {
        font-family: "Titan";
        font-size: clamp(32px,calc(40vw/14.4),54px);
        line-height: clamp(36px,calc(44vw/14.4),60px);
        margin-bottom: clamp(5px,calc(15vw/14.4),20px);
        text-transform: uppercase;
        color: #fff;
    }

    &__swiper {
        display: flex;
        height: 350px;
        width: 350px;
        overflow: hidden;
        position: relative;

        @include media(1280px) {
            width: 100%;
        }

        &-wrapper {
            display: flex;
            width: 100%;
            position: relative;
        }

        &-slide {
            flex: 1 0 100%;
            position: relative;

            & img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &-nav {
            position: absolute;
            bottom: 15px;
            right: 15px;
            display: flex;
        }

        &-prev,
        &-next {
            border: solid #fff;
            border-width: 0 7px 7px 0;
            display: inline-block;
            padding: 7px;
            cursor: pointer;
            // position: absolute;
            // top: 0;
        }

        &-next {
            transform: rotate(-45deg);
            -webkit-transform: rotate(-45deg);
        }
        
        &-prev {
            transform: rotate(135deg);
            -webkit-transform: rotate(135deg);
        }
    }

    &__contacts {
        width: 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 70px;
        gap: clamp(10px,calc(20vw/14.4),25px);
        border-left: 4px solid #fff;
        border-radius: 3px;

        @include media(1280px) {
            border-left: none;
            align-items: flex-start !important;
            text-align: left;
        }

        &-card {
            width: 100%;
            display: flex;
            align-items: center;
            gap: clamp(20px,calc(50vw/14.4),60px);
        }

        &-img {
            width: clamp(30px,calc(70vw/14.4),80px);
            height: clamp(30px,calc(70vw/14.4),80px);
            position: relative;

            & img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        &-desc {
            display: flex;
            flex-direction: column;

            & span {
                font-family: "Gochi";
                color: #fff;
                font-size: clamp(18px,calc(32vw/14.4),36px);
                line-height: clamp(24px,calc(34vw/14.4),38px);
            }
        }


        &-arrow {
            position: absolute;
            top: 10%;
            right: 3%;

            @include media(1280px) {
                display: none;
            }
        }

        &-first {
            @include media(1280px) {
                display: none;
            }
        }

        &-second {
            display: none;

            @include media(1280px) {
                width: 100%;
                height: 100%;
                display: flex;
                gap: 10px;
                justify-content: center;
                align-items: center;
            }
        }
    }


}